body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 0;
  padding: 0;
}

:root{
  --worldColor: rgb(12, 10, 22);
    /* --worldColor: black; */


}

body {
  /* background-image: url(./Components/assets/TRAITEMENTS-AIR.jpg); */
  background-color: rgb(12, 10, 22);
  background-color: var(--worldColor);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-blend-mode: color-burn;
}

.CONTAINER {
  -webkit-user-select: none;
          user-select: none;
}

.startCont{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: transparent;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: White;
    font-size: 70px;
    transition: .5s;
    -webkit-transform: scale(1);
            transform: scale(1);
    position: absolute;
    opacity: 1;
}

.startvalue{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.startvalue h1{
    color: rgb(254, 88, 88);
}

.startvalue P{
    color: rgb(159, 159, 159);
    font-size: 20PX;
}
#navbar {
  position: absolute;
  min-width: 96vw;
  min-height: 80px;
  bottom: 1%;
  margin: 0 2vw;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  transition: 1s;
}

.NameTag {
  position: absolute;
  min-width: 140px;
  min-height: 60px;
  border: solid 2px rgba(138, 138, 138, 0.614);
  border-radius: 10px;
  bottom: 80px;
  left: 80px;
  background-color: rgba(209, 209, 209, 0.833);
  z-index: 4;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s;
  font-size: 20px;
  box-shadow: 0px 0px 5px 0px grey;
  
}

.MText {
  font-size: 40px;
  margin: 0;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: 0.3s;
  -webkit-user-select: none;
          user-select: none;
}

/* rgba(253, 108, 108, 0.795) */

.NameTag h1 {
  margin: 0 20px;
  color: rgb(23, 23, 59);
  transition: 0.1s;
}

.Menu {
  background-color: rgba(216, 216, 216, 0.91);
  border: solid 2px rgb(169, 169, 169);
  width: 60px;
  height: 60px;
  position: absolute;
  top: -62px;
  left: 0%;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  transition: 0.1s;
  box-shadow: 0px 0px 5px 0px grey;
  cursor: pointer;
}

.Menu:active {
  box-shadow: 0px 0px 2px 0px rgb(60, 60, 60);
  border: solid 2px rgba(253, 108, 108, 0.795);
  transition: 0.1s;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.MenuTab {
  display: flex;
  min-width: 30%;
  min-height: 60px;
  margin: 0;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  opacity: 0;
  transition: 0.3s;
  border-radius: 0 15px 15px 15px;
  border: solid 2px rgba(183, 183, 183, 0.795);
  background-color: rgba(151, 151, 151, 0.874);
  cursor: pointer;
  box-shadow: 0px 0px 5px 2px rgba(255, 255, 255, 0.64);
}

.meuText {
  margin: 0 20px;
  color: rgb(45, 46, 79);
  font-size: 30px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 70%; */
}

.icon {
  height: 40px;
  color: brown;
  margin: 10px 0 0 0;
}

.searchBar {
  min-width: 250px;
  min-height: 100%;
  font-size: 1.6rem;
  padding: 5px;
  border-radius: 5px;
  border: none;
  box-shadow: inset 0 0 10px 3px rgb(25, 25, 64);
  text-align: center;
  color: var(--worldColor);
  background-color: rgb(184, 184, 184);
  outline: 0;
  border: 2px solid transparent;
  margin: 0;
}

.searchBar:focus {
  border: 2px solid rgb(252, 189, 200);
}


svg {
  transition: .5s;
  stroke: var(--worldColor);
}

svg path:hover {
  transition: 0.1s;
  stroke:white;
}

path{
  transition: .5s;
    cursor: pointer;

}

.svgtopbar{
  min-width: vw;
  min-height: 20px;
}

.hoverName {
  display: flex;
  position: absolute;
  background-color: rgba(173, 173, 173, 0.89);
  border-radius: 4px;
  border: 1px solid rgb(122, 122, 122);
  text-shadow: 0 0 3px black;
  z-index: 99;
  font-size: 1.5rem;
  top: -10%;
  color: WHITE;
  justify-content: center;
  opacity: 0;
}

.Hnamep {
  margin: 2px 5px;
  
}
.infoContainer {
  width: 200px;
  height: 200px;
  background-color: rgb(170, 170, 170);
  position: absolute;
  z-index: 101;
  bottom: 23%;
  left: 2%;
  border-radius: 10px;
  box-shadow: 0 0 5px grey;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  opacity: 0;
  transition: 0.5s;
  cursor: pointer;
}

.infoContainer:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  box-shadow: 0 0 20px white;
}

.infoContainer:active {
  -webkit-transform: scale(1);
          transform: scale(1);
  box-shadow: 0 0 5px rgb(255, 255, 255);
  transition: 0.1s;
}

.infoFlagCont {
  width: 87%;
  height: 100px;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 5%;
  border-radius: 5px;
  transition: 1s;
  overflow: hidden;
}

.Flag {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.infoContinent {
  position: absolute;
  bottom: 30%;
  font-size: 20px;
  /* left:8%; */
}

.infoActive {
  position: absolute;
  bottom: 18%;
  /* left: 10%; */
  font-size: 15px;
  color: rgb(1, 109, 1);
}

.infoDeaths {
  position: absolute;
  color: rgb(115, 29, 29);
  bottom: 6%;
  /* left: 10%; */
  font-size: 15px;
}

